    </el-carousel>
    <!-- 介绍 -->
    <div class="introduce">
      <div class="introduce-left">
        <img src="http://robweld.zjnu.edu.cn/zsd/img/single.5d3e5fc0.png" />
        <img src="http://robweld.zjnu.edu.cn/zsd/img/double.c4bc0545.png" />
      </div>
      <div class="introduce-right">
        <div class="title">多机器人协调焊接</div>
        <div class="desc">
          在制造强国、质量强国和数字中国的持续建设中，大飞机、高速列车、超级跨海大桥、全自动化码头等国家重大工程和大国重器不断涌现，强力催生了以机器人技术为代表的数字化、智能化、绿色化制造蓬勃兴起。大型钢结构多机器人协同焊接是制造业先进基础工艺的重要组成部分，对传统产业高端化、智能化、绿色化转型发展起到重要支撑作用。
        </div>
      </div>
    </div>
    <!-- 统计 -->
    <div class="total-wrap">
      <div class="total">
        <i class="el-icon-user-solid"></i>
        <div class="num">5138</div>
        <div class="label">总用户量</div>
      </div>
      <div class="total">
        <i class="el-icon-date"></i>
        <div class="num">4563</div>
        <div class="label">实验总次数</div>
      </div>
      <div class="total">
        <i class="el-icon-s-marketing"></i>
        <div class="num">23422</div>
        <div class="label">总访问人数</div>
      </div>
      <div class="total">
        <i class="el-icon-edit"></i>
        <div class="num">34623</div>
        <div class="label">实验总时长(min)</div>
      </div>
    </div>
    <!-- 视频 -->
    <div class="video-wrap">
      <div class="video-item">
        <div class="video">
          <video
            controls
            poster="http://robweld.zjnu.edu.cn/zsd/img/introduction.93ceb504.png"
            src="http://robweld.zjnu.edu.cn/zsd/media/introduction.3c333f07.mp4"
          ></video>
        </div>
        <div class="video-right">
          <div class="video-title">项目简介视频</div>
          <div class="video-desc">
            大型钢结构多机器人协同焊接控制实验选择被誉为“国之重器”的大型起重机箱梁数字化车间为学习场景，以典型箱体结构多机器人协同焊接运动控制、安全控制和质量控制等共性关键技术为教学目标，设计单机器焊接、双机器人协同焊接、大型起重机箱梁多机器人协同焊接“三层递进”模块，练习、考核“两种实验”模式和过程性、综合性“多元评价”标准。本实验通过再现企业的生产流程、融入企业的创新要素，着力提高学生的工程意识、创新思维及综合应用专业知识解决复杂工程问题的能力。
          </div>
        </div>
      </div>
      <div class="video-item reverse">
        <div class="video">
          <video
            controls
            poster="http://robweld.zjnu.edu.cn/zsd/img/introduction.93ceb504.png"
            src="http://robweld.zjnu.edu.cn/zsd/media/guide.e3754ec3.mp4"
          ></video>
        </div>
        <div class="video-right">
          <div class="video-title">项目引导视频</div>
          <div class="video-desc">
            实验前，学生登录门户网站查看实验目的、实验原理、实验设备、实验方法与步骤、实验考核要求等基本描述信息，进行实验认知测试。实验认知测试合格，学生方可进入实验主体模块，依次开展“单、双、多机器人”三层递进模块的仿真训练。实验结束后，系统将根据实验过程的操作准确率、用时长短和实验习题的作答情况自动给出实验成绩。此外，系统还能够生成“知识短板”，推送“个性化”学习资源，帮助学生巩固薄弱环节，充分体现“以学生为中心”持续改进理念。
          </div>
        </div>
      </div>
    </div>
    <!-- echarts -->
    <div class="visual-wrap">
