<script>
export default {
  name: 'Index',
  data() {
    return {
      swiper: [
        {
          url: 'http://robweld.zjnu.edu.cn/zsd/img/bg1.2c4ddd5e.png',
          title: '大型钢结构多机器人协同焊接控制虚拟仿真实验'
        },
        {
          url: 'http://robweld.zjnu.edu.cn/zsd/img/bg2.60783a1e.png',
          title: '大型钢结构多机器人协同焊接控制虚拟仿真实验'
        },
        {
          url: 'http://robweld.zjnu.edu.cn/zsd/img/bg3.42d8432c.png',
          title: '大型钢结构多机器人协同焊接控制虚拟仿真实验'
        }
      ],
      tableData: [
        {
          sort: 1,
          province: '江苏',
          personNum: 80
        },
        {
          sort: 2,
          province: '浙江',
          personNum: 20
        },
        {
          sort: 3,
          province: '安徽',
          personNum: 60
        },
        {
          sort: 4,
          province: '山东',
          personNum: 20
        },
        {
          sort: 5,
          province: '广东',
          personNum: 70
        },
        {
          sort: 6,
          province: '北京',
          personNum: 50
        },
        {
          sort: 7,
          province: '黑龙江',
          personNum: 30
        },
        {
          sort: 8,
          province: '福建',
          personNum: 40
        }
      ],
      charts1: null,
      charts2: null
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initCharts1()
      this.initCharts2()
    })
  },
  methods: {
    initCharts1() {
      this.charts1 = window.echarts.init(this.$refs.echarts1)
      const option = {
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          bottom: '2%',
          left: 'center',
          textStyle: {
            rich: {
              oneone: {
                width: 70
              },
              twotwo: {
                width: 70
              },
              threethree: {}
            }
          },
          formatter: function (name) {
            var data = option.series[0].data
            var total = 0
            var tarValue
            for (var i = 0; i < data.length; i++) {
              total += data[i].value
              if (data[i].name == name) {
                tarValue = data[i].value
              }
            }
            var v = tarValue
            var p = Math.round((tarValue / total) * 100)
            return `{oneone|${name}}  {twotwo|${v}}  {threethree|${p}%}`
          }
        },
        grid: {
          top: '5%',
          left: 0
        },
        series: [
          {
            name: '用户数量',
            type: 'pie',
            radius: ['40%', '60%'],
            center: ['50%', '35%'],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: true,
                position: 'center',
                color: '#4c4a4a',
                formatter: '{total|' + 100 + '}',
                rich: {
                  total: {
                    fontSize: 35,
                    fontFamily: '微软雅黑',
                    color: '#454c5c'
                  },
                  active: {
                    fontFamily: '微软雅黑',
                    fontSize: 16,
                    color: '#6c7a89',
                    lineHeight: 30
                  }
                }
              },
              emphasis: {
                //中间文字显示
                show: true
              }
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '40',
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: false
            },
            data: [
              { value: 1000, name: '高校学生' },
              { value: 735, name: '教师' },
              { value: 580, name: '评审专家' },
              { value: 484, name: '管理员' }
            ]
          }
        ]
      }
      this.charts1.setOption(option)
      window.addEventListener('resize', () => {
        this.charts1.resize()
      })
    },
    initCharts2() {
      this.charts2 = window.echarts.init(this.$refs.echarts2)
      const option = {
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          bottom: '2%',
          left: 'center',
          textStyle: {
            rich: {
              oneone: {
                width: 70
              },
              twotwo: {
                width: 70
              },
              threethree: {}
            }
          },
          formatter: function (name) {
            var data = option.series[0].data
            var total = 0
            var tarValue
            for (var i = 0; i < data.length; i++) {
              total += data[i].value
              if (data[i].name == name) {
                tarValue = data[i].value
              }
            }
            var v = tarValue
            var p = Math.round((tarValue / total) * 100)
            return `{oneone|${name}}  {twotwo|${v}}  {threethree|${p}%}`
          }
