                show: true,
                position: 'center',
                color: '#4c4a4a',
                formatter: '{total|' + 100 + '}',
                rich: {
                  total: {
                    fontSize: 35,
                    fontFamily: '微软雅黑',
                    color: '#454c5c'
                  },
                  active: {
                    fontFamily: '微软雅黑',
                    fontSize: 16,
                    color: '#6c7a89',
                    lineHeight: 30
                  }
                }
              },
              emphasis: {
                //中间文字显示
                show: true
              }
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '40',
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: false
            },
            data: [
              { value: 100, name: '>=90' },
              { value: 735, name: '80~90' },
              { value: 580, name: '70~80' },
              { value: 484, name: '60~70' },
              { value: 84, name: '<60' }
            ]
          }
        ]
      }
      this.charts2.setOption(option)
      window.addEventListener('resize', () => {
        this.charts2.resize()
      })
    }
  }
}
</script>

